/* 기본 사이드바 스타일 */
.sidebar {
  width: 200px !important; /* 우선순위 강제 */
  background-color: #f8f8f8 !important;
  padding: 20px 10px !important;
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  height: 100% !important;
  z-index: 4 !important;
  transition: width 0.3s ease !important; /* 부드러운 애니메이션 */
}

/* 회사명 숨기기 (사이드바가 좁을 때) */
.company-name {
  display: none;
}

/* 마우스 오버 시 회사명 표시 */
.sidebar:hover .company-name {
  display: block;
}

/* 프로필 원이 항상 가운데 정렬되도록 유지 */
.profile-circle {
  width: 50px;
  height: 50px;
  background-color: #ddd;
  border-radius: 50%;
  margin: 0 auto;
}

/* 메뉴 항목 스타일 */
.menu {
  margin-top: 60px;
}

.menu-item {
  padding: 10px 5px; /* 좁을 때는 간격을 줄임 */
  margin: 5px 0;
  background-color: #f8f8f8;
  cursor: pointer;
  text-align: center;
}

/* 마우스 오버 시 메뉴 아이템의 패딩을 확장 */
.sidebar:hover .menu-item {
  padding: 10px 20px; /* 확장된 상태에서 더 넓은 패딩 적용 */
}

.menu-item:hover {
  background-color: #EAEAEA;
}

.menu-item.selected {
  background-color: #ddd;
  font-weight: bold;
}

.settings-menu {
  margin-top: 20vh;
}